.step-slider {
  background-color: #001f1e;
}

.step-slider__container {
  background-color: #ecece8;
  padding: 9.23188rem 0 12.23188rem 3.03865rem;

  @include for-desktop {
    padding: 10.41667rem 0;
    display: flex;
    justify-content: space-between;
    margin-left: 6.94444rem;
  }

  @include mobile {
    padding: 9.23188rem 0 12.23188rem 0.03865rem;
  }
}

.step-slider__content {
  color: #001f1e;
  margin-bottom: 5.32367rem;

  @include for-desktop {
    display: flex;
    flex-direction: column;
    width: 35.55556rem;
    padding-left: 8.54167rem;
    margin: 0;
    background-color: #ecece8;
  }

  @include mobile {
    padding-left: 0rem;
  }
}

.step-slider__title {
  width: 20.5942rem;
  margin-right: auto;
  margin-left: auto;
  text-align: center;

  @include for-desktop {
    width: 17.70833rem;
    margin-bottom: 2.08333rem;
    text-align: left;
    margin-left: 0;
    margin-right: 0;
  }

  @include mobile {
    width: 95%;
    margin: 0 auto; /* Центрируем слайд по горизонтали */
    text-align: center;
  }
}

.step-slider__controls {
  align-items: center;
  grid-gap: 2.41546rem;
  gap: 0.61546rem;

  @include for-desktop {
    grid-gap: 0.69444rem;
    gap: 0.69444rem;
  }
}

.step-slider__controls.desktop {
  display: none;
  margin-top: auto;

  @include for-desktop {
    display: flex;
  }
}

.step-slider__controls.mobile {
  display: flex;
  justify-content: center;
  margin-top: 8.32367rem;
  margin-right: auto;
  margin-left: auto;

  @include for-desktop {
    display: none;
  }
}
.step-slider__slider {
  width: 100%;
  max-width: 45rem; /* Увеличьте максимальную ширину слайдера на ваш выбор */
  margin: 0 auto; /* Центрируем слайдер по горизонтали */
  height: 48rem; /* Увеличьте высоту слайдера на ваш выбор */

  @include for-desktop {
    margin: 0;
    max-width: 59rem;
    width: 51.25rem;
    min-height: 30rem; /* Уменьшите минимальную высоту слайдера на ваш выбор */
    height: auto; /* Автоматическая высота, чтобы соответствовать содержимому */
  }

  @include mobile {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    height: 30rem;
  }
}

.step-slider__slide {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ecece8;
  color: #001f1e;
  margin: 0 auto; /* Центрируем слайд по горизонтали */
  margin-bottom: 4rem; /* Увеличьте или уменьшите отступ между слайдами на ваш выбор */
  padding: 3rem; /* Увеличьте внутренний отступ слайда на ваш выбор */
  will-change: transform;
  height: 100%;
  border: 1px solid #595a58;
  border-right: none;
  width: 90%; /* Заполните доступное пространство */

  @include for-desktop {
    margin: 0;
    padding: 2.5rem 4.02778rem; /* Уменьшите внутренний отступ слайда для десктопа на ваш выбор */
    width: 30.02778rem;
  }

  @include mobile {
    width: 95%;
    padding: 1.5rem 2.02778rem; /* Уменьшите внутренний отступ слайда для десктопа на ваш выбор */

    margin-left: auto;
    margin-right: auto;

    border-right: 1px solid #bebfbb;
  }
}

.step-slider__slide.firstStep .step-slider__number {
  margin-bottom: 1.52778rem;
}

.step-slider__slide:last-child {
  border-right: 1px solid #bebfbb;
}

.step-slider__slide.is-selected .step-slider__number {
  color: #fff;
  background-color: #001f1e;
}

.step-slider__slide-text {
  font-family: Basis Grotesque Pro;
  font-size: 2.38164rem;
  line-height: 3.31401rem;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;

  @include for-desktop {
    font-size: 1.11111rem;
    line-height: 1.94444rem;
  }

  @include mobile {
    font-size: 1.11111rem;
    line-height: 1.94444rem;
  }
}

.step-slider__slide-title {
  font-family: Freight;
  font-size: 4.12947rem;
  line-height: 5.66184rem;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;

  @include for-desktop {
    font-size: 3.4167rem;
    line-height: 4.16667rem;
  }

  @include mobile {
    font-size: 2.54167rem;
    line-height: 2.16667rem;
  }
}

.step-slider__number {
  margin-top: auto;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 100px;
  background-color: #fff;
  color: #001f1e;
  transition:
    background-color 0.8s cubic-bezier(0.85, 0, 0.15, 1) 0.3s,
    color 0.8s cubic-bezier(0.85, 0, 0.15, 1) 0.3s;
  font-size: 1.97222rem;
  margin-bottom: 2.15278rem;

  padding: 1.19444rem 2.38889rem;
  text-transform: uppercase;

  @include for-desktop {
    margin-bottom: 2.15278rem;
    padding: 0.69444rem 1.38889rem;
    font-size: 0.97222rem;
  }

  @include mobile {
    font-size: 0.97222rem;
    padding: 0.69444rem 1.38889rem;
    margin-bottom: 2.15278rem;
  }
}

.step-slider__image-container {
  align-items: center;
  display: flex;
  width: 100%;
  height: 50%;
}

.step-slider__image-container img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}
