.carousel {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 230px;

  overflow: hidden;
  background-color: #82a89c;
}

.carousel__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
  width: calc(200px * 16); // Удвоенное количество элементов
}

.carousel__item {
  flex: 0 0 200px;
  height: 200px;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #acbbb6;
  border-radius: 10px;

  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }


}
