.method-card-section {
  background-color: #000f0a;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  overflow: hidden;
}

.method-card {
  position: relative;
  background-color: #ebf7f6;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 90px;
  height: auto;


  @include middle-tablet {
    width: 50%;
    align-items: flex-start;
    padding: 0;

    .method-card__diagrams .bulb svg {
      display: none;
    }
  }

  @include mobile {
    width: 80%;
  }
}

.method-card__text {
  position: relative; // changed from absolute
  right: 15%; // changed from fixed pixel value
  top: 0; // it's ok to keep this as 0
  font-size: 18px;
  font-weight: bold;
  font-family: var(--second-family);
  width: 50%; // give some space for margins
  color: #000;

  @include middle-tablet {
    left: 1rem;
    width: 100%; // changed to width instead of min-width
    font-size: 14px;
    padding: 15px;
  }

  @include mobile {
    // for smaller mobile devices
    font-size: 16px; // reduce font-size a bit
    padding: 20px; // reduce padding
    width: 100%;
    left: 0;
  }
}

.bulb__cta--mobile {
  display: none; // It's hidden by default on small devices

  @include middle-tablet {
    display: flex; // It remains visible on larger devices
    color: black;
    font-size: 1.4rem; // The font size increases as the viewport gets larger
    font-weight: bold;
    bottom: 2rem;

  }
}

.method-card__diagrams {
  position: relative;
  top: 1rem;

  @include middle-tablet {

    right: auto;
    align-items: flex-start; // Добавить это
    margin-left: 1rem; // Добавить это
    height: 32rem;
  }



  .bulb {
    opacity: 0.2;
    transition: opacity 0.5s ease;

    @include middle-tablet {
      opacity: 1;

    }

    .captions {
      .bulb__caption {
        opacity: 0;
        transition: opacity 0.5s ease;

        @include middle-tablet {
          opacity: 1;

        }
      }
    }
  }

  .bulb--active {
    opacity: 1;

    .captions {
      .bulb__caption {
        opacity: 1;

        @include middle-tablet {
          opacity: 1;

        }
      }
    }
  }

  .bulb--top {
    position: relative;
    transform: scaleY(-1);

    @include middle-tablet {
      top: 1.5rem;
    }

    .captions {
      display: block;

      @include middle-tablet {
        display: flex;
        flex-direction: column;
        align-items: flex-start; // Added this line
      }

      .bulb__caption {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgb(0, 0, 0);
        font-size: 14px;
        font-weight: 400;
        width: 200px;
        height: 50px;
        background-color: rgba(255, 255, 255, 0.8);
        border-radius: 20px;
        pointer-events: none;
        transform: scaleY(-1);

        @include middle-tablet {
          position: relative;
          border: none;
          background: transparent;
          color: #000;
          align-items: flex-start; // Added this line
          justify-content: flex-start; // Added this line
          margin-bottom: -15px; // Adjust this value according to your needs
        }

        p {
          margin: 0;
          padding: 5px;
        }
      }

      .bulb__caption:nth-child(1) {
        top: 5em;
        /* (70px / 14px) */
        left: -10.714em;
        /* (-150px / 14px) */

        @include middle-tablet {
          top: auto;
          left: auto;
        }
