  }

  .bulb__ctas {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 70px auto;

    @include middle-tablet {
      justify-content: flex-start;
    }

    .bulb__cta {
      cursor: pointer;
      position: relative;
      color: #000;
      margin: 60px 30px;

      @include middle-tablet {
        position: relative;
        margin: 0;
        cursor: none;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: -5px;
        height: 2px;
        background: black;
        transform: scaleX(0);
        transform-origin: left;
        transition: transform 0.6s;
      }

      &:hover::after,
      &.bulb__cta--active::after {
        transform: scaleX(1);
      }

      @include middle-tablet {
        margin: 0;
        top: 1rem;
        font-size: 1.4rem;
        font-weight: bold;
      }
    }

    .some-other-class {
      @include middle-tablet {
        display: none;
      }
    }

    @include middle-tablet {
      margin: 0;
    }
  }

  @include middle-tablet {
    .bulb__cta--active {
      display: none !important;
    }
  }

