.header {
  padding: 30px 0;
  transition: all 0.3s ease-in-out;
  /* Add transition */

  &.scrolled {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #000f0a;
    opacity: 1;
    transition: all 0.3s ease-in-out;
    /* Add transition */

    @include middle-tablet {
      background-color: transparent;
    }
  }
}

.header__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; /* Задайте каждому столбцу одинаковую ширину */
  align-items: center;
  justify-content: space-between;
  text-align: center;
  margin: 0 auto;
  position: center;

  @include middle-tablet {
    display: flex;
    padding: 0;
  }
}

.header--position {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
}

.logo {
  display: flex;
  align-items: center;
  z-index: 20;
  transition: filter 0.3s ease-in-out;
  margin-right: 10px; /* Добавьте отступ справа, чтобы сдвинуть логотип влево */


  @include middle-tablet {

  }
}

.logo.black {
  filter: brightness(100%);

  @include middle-tablet {
    filter: brightness(0%);
  }
}

.logo img {
  height: 100px;
  /* Set a fixed height for the logo */
  width: auto;
  /* Let the width adjust automatically */
  max-height: 100%;
  /* Ensure the logo does not exceed its parent container */
  max-width: 100%;
  /* Ensure the logo does not exceed its parent container */
}

.logo__title {
  margin-left: 0vw;
  /* Adjust the margin as needed */
  font-size: 1rem;
  /* Set a fixed font size for the logo title */
  /* Adjust the font size as needed */
  color: #fff;
  /* Set the font color to white */

  @include middle-tablet {
    display: none;
  }
}

.nav {
  flex: 1;
  align-items: center;
  justify-content: center;
  text-align: center;

  @include middle-tablet {
    display: none;
  }
}

.nav__list {
  gap: 16px;
}

.nav__link {
  font-size: 16px;
  color: var(--light-color);
  transition: color 0.3s ease-in-out;
}

.nav__link:focus {
  outline: none;
}

.nav__link:hover {
  color: rgba(233, 237, 97, 0.733);
}

.nav__link:active {
  color: var(--active-color);
}

.nav__link.focus-visible {
  outline: 1px solid var(--active-color);
  outline-offset: 3px;
}

.column-three-content {
  display: flex;
  justify-content: flex-end;
  gap: 10px; /* Adjust as needed */
  flex-wrap: nowrap; /* Гарантирует, что элементы не переноситься на новую строку */

  @include middle-tablet {
    display: none;
  }
}

.header__socials {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin-right: 10px;
  list-style: none;
  /* Добавим отступ справа */

  @include middle-tablet {
    display: none;
  }
}

.header__socials-item {
  margin-left: 10px;
}

.header__socials-item:last-child {
  margin-right: 0;
  /* Заменим "margin-left" на "margin-right" для последнего элемента */
}

.header__socials-item img {
  width: 2vw;
  /* Задайте ширину изображения в процентах от ширины экрана */
  height: auto;
  /* Сохраните пропорции изображений */
  max-height: 100px;
  /* Задайте максимальную высоту изображения */
  max-width: 100px;
  /* Задайте максимальную ширину изображения */
  filter: brightness(0) invert(1);
}

.header__lang-wrapper {
  display: flex;
  align-items: center;
  margin-left: 1rem;

  @include middle-tablet {
    display: none;
  }
}

.header__lang {
  height: 2rem;
  background-color: transparent;
  /* Изменяем цвет фона на темно-зеленый */
  color: white;
  /* Устанавливаем белый цвет текста */
  padding: 0rem 0.5rem;
  /* Добавляем отступы внутри элемента */
  border-radius: 1rem;
  /* Устанавливаем радиус границы */

  @include middle-tablet {
    display: none;
  }
}

.burger-menu {
  display: none;

  @include middle-tablet {
    display: block;
    cursor: pointer;
    width: 30px;
    height: 20px;
    position: relative;
    z-index: 10;
    padding-right: 3.5rem;
  }

  .burger-menu__button {
    cursor: pointer;
    width: 25px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .burger-menu__line {
      background-color: #fff;
      height: 2px;
      width: 100%;
      transition: all 0.3s ease;

      &.black {
        background-color: #000;
      }
    }

    .burger-menu__line:nth-child(2) {
      z-index: 20;

      .close {
        display: none;
        position: absolute;
        top: -3px; // Adjust as necessary
        right: 35px;
        color: #fff;
        font-size: 1.2rem; // Adjust as necessary
        align-items: center;
        gap: 10px; // Adjust the gap between text and line as necessary
        cursor: pointer;

        span {
          margin-right: 2rem;
          margin-top: 1px;
        }
      }
    }

    &.open {
      .burger-menu__line:nth-child(1),
      .burger-menu__line:nth-child(3) {
        transform: scale(0);
      }
    }
  }

  .burger-menu__container {
    width: 100%;
    height: 100%;

    .burger-menu__nav {
      position: fixed;
      top: 0;
      right: 0;
      width: 0;
      height: 0;
      overflow: hidden;
      transition: all 0.6s ease-in-out;
      background-color: rgb(0, 0, 0);
      z-index: 9;
      align-items: center;
      justify-content: center;
      display: flex;
      flex-direction: column;
      padding-top: 6px;
      transform: translateY(100%);
      opacity: 0;
      transition:
        transform 0.4s ease,
        opacity 0.5s ease;

      &.open,
      &.nav-animate {
        width: 100%;
        height: 100%;
        min-height: 1000px;
        transform: translateY(0%);
        opacity: 1;
      }

      &.close {
        width: 100%;
        min-height: 1000px;
        height: 100%;
        transform: translateY(100%);
        opacity: 0;
        transition:
          transform 0.7s ease,
          opacity 0.5s ease;
      }

      .burger-menu__lang-wrapper {
        position: absolute;
        top: 20%; /* Пример использования процентов */
        left: 3.5em;
        order: 1;
        animation-name: slideIn;
        animation-duration: 0.5s;
        animation-fill-mode: both;
        animation-delay: 0.6s;

        .burger-menu__lang {
          height: 35px;
          background-color: transparent;
          /* Изменяем цвет фона на темно-зеленый */
          color: white;
          /* Устанавливаем белый цвет текста */
          padding: 0.5rem 1rem;
          /* Добавляем отступы внутри элемента */
          border-radius: 0.9rem;
          /* Устанавливаем радиус границы */
        }
      }

      ul {
        justify-content: center;
        align-items: flex-start;
        width: 100%;
        flex-direction: column;
        order: 2;

        li {
          padding: 5px;
          margin: 10px 50px;
          animation-name: slideIn;
          animation-duration: 0.5s;
          animation-fill-mode: both;
          animation-delay: 0.4s;
        }

        a {
          color: #fff;
          font-size: 2em;
          text-decoration: none;
        }
      }

      .burger-menu__socials {
        position: absolute;
        bottom: 5%; /* Пример использования процентов */
        order: 3;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        list-style: none;
        animation-name: slideIn;
        animation-duration: 0.5s;
        animation-fill-mode: both;
        animation-delay: 0.8s;

        .burger-menu__socials-item {
          margin: 0 0.5em;
          /* adjust the values as per your requirements */

          img {
            width: 90%;
            height: auto;
            max-height: 100px;
            max-width: 100px;
            filter: brightness(0) invert(1);
          }
        }
      }
    }

    @keyframes slideIn {
      0% {
        transform: translateY(100%);
        opacity: 0;
      }

      100% {
        transform: translateY(0);
        opacity: 1;
      }
    }

    @keyframes slideOut {
      0% {
        transform: translateY(0);
        opacity: 1;
      }

      100% {
        transform: translateY(100%);
        opacity: 0;
      }
    }

    @keyframes slideOutTop {
      0% {
        transform: translateY(0);
        opacity: 1;
      }

      100% {
        transform: translateY(-100%);
        opacity: 0;
      }
    }

    .slideIn {
      animation: slideIn 0.5s forwards;
    }

    .slideOut {
      animation: slideOut 0.5s forwards;
    }

    .slideOutTop {
      animation: slideOutTop 0.5s forwards;
    }
  }
}
