.title-text-sample {
  background-color: #000f0a;
  display: flex;
  justify-content: center;
  overflow: auto; // Enable scrollbars when content exceeds the container's boundaries
  width: 100%; // Width set to 100%
  padding-bottom: 100px;

  .container {
    max-width: 1000px;
    width: 100%;
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px; // Padding added to prevent content from touching the edges
    margin-top: 7rem;

    .title-text-sample__title {
      text-align: center;
      width: 90%;

      @include mobile {
        width: 100%;
        text-align: left;
      }
    }

    .title-text-sample__text-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;

      @include small-tablet {
        flex-direction: column;
        align-items: center;
      }

      .title-text-sample__text,
      .title-text-sample__text--second {
        width: 45%;
        text-align: justify;
        margin-top: 8%;
        line-height: 1.2em;

        @include small-tablet {
          width: 100%;
        }
      }
    }
  }

  &.padding {
    padding-bottom: 0px;

  }

  &.small-size {
    min-height: 40vh; // Reduce the minimum height to 50% of the viewport height
    // Add any other changes you want to make here
  }

  &.color {
    background-color: #ebf7f6;
    color: rgb(0, 0, 0); // Change the color of text in this section

    // If you also want to change the color of headings and paragraphs, you can do so like this:
    h2,
    p {
      color: rgb(0, 0, 0); // Change the color of headings and paragraphs
    }
  }

  &.contact {
    background-color: #ebf7f6;
    min-height: 40vh;
    color: rgb(0, 0, 0); // Change the color of text in this section
    h2 {
      color: rgb(0, 0, 0); // Change the color of headings and paragraphs
    }
  }
}
