.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 80px;
  padding: 17px 33px;
  font-size: var(--main-size);
  line-height: 20px;
  letter-spacing: 0.02px;
  text-align: center;
  color: white;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out, color 0.5s ease-in-out;
  position: relative;
  /* Added position: relative */
  top: 5rem;
  /* Adjusted top position to 2rem */
}

.btn:focus {
  outline: none;
}

.btn:hover {
  border-color: white;
  background-color: rgba(195, 195, 195, 0.344);
  color: black;
}

.btn:active {
  border-color: rgb(255, 255, 255);
  background-color: rgb(255, 255, 255);
}

.btn.focus-visible {
  border-color: var(--active-color);
}
