.footer {
  background-color: #0a1a1a;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  padding-top: 5rem;
  margin-left: auto;
  margin-right: auto;
  color: white;


}

.footer::after {
  content: "";
  width: 100%;
  height: 0.5px;
  background-color: rgba(255, 255, 255, 0.623);
  position: absolute;
  bottom: 5rem;
  left: 0;

  @include mobile{
    margin-bottom: 19rem;
  }

}

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  grid-gap: 9.66184rem;
  gap: 1.16184rem;
  margin-left: auto;
  margin-right: auto;

}

.footer__navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  @include mobile {
    flex-direction: column;
  }

}

.footer__logo {
  margin-bottom: 1rem;
  max-width: 20%;
  justify-content: center;
  position: relative;
  display: flex;
  margin-left: auto;
  margin-right: auto;


  @include mobile {
    display: grid;
    grid-gap: 2.83092rem 11.35266rem;
    gap: 1.13092rem 3.35266rem;
  }
}

.footer__pages {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 9rem;
  margin-left: auto;
  margin-right: auto;


  @include mobile {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2.83092rem 11.35266rem;
    gap: 1.13092rem 3.35266rem;
    margin-bottom: 3.2rem;


  }

}

.footer__pages-item {
  margin: 0 1rem;

  @include mobile {
    margin: 0;

  }
}

.footer__pages-item a {
  color: #fff;
}

.footer__information {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;

  @include mobile{
  display: grid;
  height: 500px;
  grid-template-columns: repeat(3,3fr);
  grid-template-rows: repeat(3,3fr);
}
}

.footer__information a {
  color: white;
}

.footer__lang-wrapper {
  display: flex;
  justify-content: flex-start;
  margin-right: auto;

  @include mobile {
    margin-right: 0;
    margin-left: auto;
    justify-content: flex-end;
  }
}

.footer__lang {
  height: 2rem;
  background-color: transparent;
  color:white;
  padding: 0 1rem;
  border-radius: 0.9rem;
  margin-right: 2vw;
  margin-left: 0;

@include mobile{
margin-right: auto;
  margin-left: auto;

}
}

.footer__socials {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  list-style: none;

@include mobile{


  margin: 0;
 justify-content: center;
  padding: 0;
  grid-column: 2/ -2;
  grid-row: 2;
  align-items: center;


}

}

.footer__socials-item {
  margin-right: 1vw;


}

.footer__socials-item:last-child {
  margin-right: 0;
}

.footer__socials-item img {
  width: 2vw;
  height: auto;
  max-height: 100px;
  max-width: 100px;
  filter: brightness(0) invert(1);

  @include mobile{
    height: 60px;
    width: 60px;
  }
}

.footer__copyrights {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1rem;
  width: 100%;
  height: 100px;
  text-align: center;
  flex-direction: column;

@include mobile{

  grid-row: 3;
    grid-column: 1 / -1;
    align-items: center;
    justify-content: center;
    font-size: 0.7rem;
    font-weight: bold;
    padding-left: 0;


}


}


