.contact {
  background-color: #0a1a1a;



  &__container {
    display: grid;
    grid-template-columns: minmax(0, 1.3fr) minmax(0, 1fr);
    grid-gap: 1rem;
    background-color: #ebf7f6;
    color: #000000a4;
    padding: 6.15459rem 0;
    width: 90%;

    @include middle-tablet{
      width: 100%;
    }

    @include small-tablet {
      grid-template-columns: 1fr;
      /* Изменяем количество колонок на 1 */
      justify-items: center;
      /* Центрируем элементы по горизонтали */
      padding: 3rem 0;
      /* Изменяем отступы */
    }

  }

  &__title {
    text-transform: uppercase;
    margin-left: 10rem;
    margin-top: -1.5rem;
    font-family: var(--font-family);

    @include small-tablet {
      margin-left: 0;
      /* Убираем отступ слева */
      text-align: center;
      /* Выравниваем текст по центру */
    }

  }

  &__content {
    max-width: 60vh;
    font-family: var(--second-family);

    @include small-tablet {
      max-width: 60%;
      /* Убираем ограничение на максимальную ширину */
      justify-self: center;
      /* Центрируем элемент по горизонтали */
      align-self: center;
      /* Центрируем элемент по вертикали */
    }

  }

  &__link {
    opacity: 0.6 !important;
    position: relative;
    padding-left: 5.83092rem; // Adjust the padding-left value as needed

    @include small-tablet {
      padding-left: 0;
      /* Убираем отступ слева */
      text-align: center;
      /* Выравниваем текст по центру */

    }

    &:before {
      content: "";
      position: absolute;
      left: 11rem; // Adjust the left value as needed
      top: 50%;
      transform: translateY(-50%);
      width: 2.32319rem;
      height: 2.32319rem;
      background-image: url(../../img/svg/black.svg);
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: contain;

      @include small-tablet {
        width: 1rem;
        /* Adjust the width value as needed */
        height: 1rem;
        /* Adjust the height value as needed */
        left: 0;
        /* Устанавливаем начало стрелки в левом углу */
      }

    }
  }
}
