* {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  overscroll-behavior-y: none;
}

@supports (-webkit-touch-callout: none) {
  body {
    min-height: -webkit-fill-available;
  }
}

.page__body {
  margin: 0;
  min-width: 20rem;
  min-height: 100%;
  font-size: 1rem;
}

body,
html {
  position: relative !important;
  overflow: auto !important;
  overscroll-behavior-y: none !important;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a,
body,
div,
h1,
html,
img,
li,
nav,
p,
section,
span,
ul {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

nav,
section {
  display: block;
}

main {
  display: block;
}

li,
ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
}

input {
  border: 0;
}

input,
select {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

select {
  margin: 0;
  -webkit-margin: 0;
  border-radius: 0;
  -webkit-border-radius: 0;
}

input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

:root {
  --vh: 1vh;
  --vw: 1vw;
  --scrollbar-widthvw: 0;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.js-scroll-content {
  width: 100%;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  column-gap: var(--offset);
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
}

.element {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s, transform 1s;
}

.element.animate {
  opacity: 1;
  transform: translateY(0);
}



.scroll-disabled {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
}


.round-button {
  cursor: pointer;
  overflow: hidden;
  position: relative;
  height: 5.15459rem;
  width: 5.15459rem;
  transition: transform .5s cubic-bezier(.25,1,.5,1);
  will-change: opacity;
}

.round-button:hover {
  transform: scale(0.7);
}

.round-button.isDisable {
  opacity: .3;
  pointer-events: none;
}

.round-button__arrow {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 1.13092rem;
  height: 1.13092rem;
  margin: auto;
  background-position: 50%;
  pointer-events: none;
  background-size: cover;
  background-color: transparent;
  border: none;
}

.round-button .round-button__circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: transform .5s cubic-bezier(.25,1,.5,1);
}

.round-button--white .round-button__circle {
  background-color: #fff;
}

.round-button--green .round-button__circle {
  background-color: #001f1e;
}

.round-button--black .round-button__circle {
  background-color: #000;
}

.round-button--left {
  transform: scaleX(1);
}

.round-button--right {
  transform: scaleX(1);
}

.text-image {
  position: relative;
  overflow: hidden
}
