.services {
  position: relative;
  height: 800px;
  background-color: #0a1a1a;
  overflow: hidden;
  width: 100%;

  @include mobile {
    height: 600px;
  }
}

.services__content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 49.4%;
  transform: translate(-50%, -50%);

  @include mobile{
    width: 80%;

  }

}

.services__container {
  max-width: 25.60386rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--light-color);
  margin-left: 1.5rem;

@include mobile{
  width: 100%;
  margin-top: 1.2rem;

}


}

.services__image-wrapper {
  position: relative;
  width: 70%;
  height: 70%;

  @include mobile {
    width: 80%;
  }

}

.services__image-container {
  position: absolute;
  width: 32%;
  height: 32%;
  top: calc(50% - (32% / 2) + 6.25rem);
  left: calc(50% - (23% / 2));
  transform-origin: center center;
  border-radius: 50%;
  transition: opacity 0.3s;



}

.services__image-container:hover {
  opacity: 1;



}

.services__image-container:nth-child(1) {
  transform: rotate(0deg) translate(18rem) rotate(0deg);

  @include small-tablet {
    transform: rotate(0deg) translate(14rem) rotate(0deg);
  }

  @include mobile {
    transform: rotate(0deg) translate(8.5rem) rotate(0deg);
  }

}


.services__image-container:nth-child(2) {
  transform: rotate(45deg) translate(18rem) rotate(-45deg);

  @include small-tablet {
    transform: rotate(45deg) translate(14rem) rotate(-45deg);
  }

  @include mobile {
    transform: rotate(45deg) translate(10rem) rotate(-45deg);
  }

}

.services__image-container:nth-child(3) {
  transform: rotate(90deg) translate(18rem) rotate(-90deg);

  @include small-tablet {
    transform: rotate(90deg) translate(14rem) rotate(-90deg);
  }

  @include mobile {
    transform: rotate(90deg) translate(11.5rem) rotate(-90deg);
  }

}

.services__image-container:nth-child(4) {
  transform: rotate(135deg) translate(18rem) rotate(-135deg);

  @include small-tablet {
    transform: rotate(135deg) translate(14rem) rotate(-135deg);
  }


  @include mobile {
    transform: rotate(135deg) translate(10rem) rotate(-135deg);
  }
}

.services__image-container:nth-child(5) {
  transform: rotate(180deg) translate(18rem) rotate(-180deg);

  @include small-tablet {
    transform: rotate(180deg) translate(14rem) rotate(-180deg);
  }


  @include mobile {
    transform: rotate(180deg) translate(8.5rem) rotate(-180deg);
  }
}

.services__image-container:nth-child(6) {
  transform: rotate(225deg) translate(18rem) rotate(-225deg);

  @include small-tablet {
    transform: rotate(225deg) translate(12rem) rotate(-225deg);
  }

  @include mobile {
    transform: rotate(225deg) translate(10rem) rotate(-225deg);
  }

}

.services__image-container:nth-child(7) {
  transform: rotate(270deg) translate(18rem) rotate(-270deg);

  @include small-tablet {
    transform: rotate(270deg) translate(12rem) rotate(-270deg);
  }

  @include mobile {
    transform: rotate(270deg) translate(10rem) rotate(-270deg);
  }

}

.services__image-container:nth-child(8) {
  transform: rotate(315deg) translate(18rem) rotate(-315deg);

  @include small-tablet {
    transform: rotate(315deg) translate(12rem) rotate(-315deg);
  }

  @include mobile{
    transform: rotate(315deg) translate(10rem) rotate(-315deg);
  }

}

.services__image {
  object-fit: cover;
  filter: grayscale(100%);
  opacity: 0.1;
  will-change: opacity;
  transform: scale(0.8);

  @include small-tablet {
    opacity: 0.1;
  }

}

.services__title {
  text-transform: uppercase;
  font-size: 1.46473rem;
  line-height: 1.5rem;
  font-family: var(--font-family);

  @include mobile {
    font-size: 1.26473rem;
    line-height: 1rem;
    text-transform: capitalize;

  }

}

.services__paragraph {
  font-size: 0.78164rem;
  line-height: 1.18164rem;

@include mobile{
  font-size: 0.88164rem;
  line-height: 1rem;
}
}

.services__link {
  display: flex;
  font-size: 1.38164rem;
  line-height: 3.38164rem;
  color: rgb(255, 255, 255);
  opacity: 0.6 !important;
  position: relative;
  padding-left: 1.73611rem;
  margin-right: 2rem;

@include mobile{
  font-size: 1.48164rem;
  line-height: 2rem;
  margin-right: 1.3rem;

}
}

.services__link:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 1.04167rem;
  height: 1.04167rem;
  background-image: url(../../img/svg/link-arrow.svg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
}



