.testimonials {
  padding: 12.5rem 0;
  background-color: #000f0a;
  color: white;
}

.testimonials__container {
  display: flex;
  justify-content: space-between;
  margin-left: 7.38889rem;
  min-height: 38.05556rem;

  @include middle-tablet {
    flex-direction: column;
    align-items: center;
    justify-content: normal;
    margin: 0 auto;
  }
}

.testimonials__content {
  display: flex;
  flex-direction: column;
  width: 21.04167rem;
  padding: 0;
  margin: 0;

  @include middle-tablet {
    width: 100%;
    align-items: center;
  }
}

.testimonials__sub-title,
.testimonials__title {
  margin-bottom: 2.08333rem;
  font-size: 1.25rem;
  line-height: 1.94444rem;
  text-transform: uppercase;
  font-family: Basis Grotesque Pro;

  @include mobile{
    font-size: 1rem;
    line-height: 100%;
  }
}

.testimonials__main {
  font-family: Basis Grotesque Pro;
  font-weight: 400;
  font-size: 1.11111rem;
  line-height: 1.94444rem;

  @include middle-tablet {
    width: 80%;
  }
}

.testimonials__controls {
  align-items: center;
  grid-gap: 0.69444rem;
  gap: 0.69444rem;

  @include middle-tablet {
    align-items: center;
    grid-gap: 2.41546rem;
    gap: 2.41546rem;
  }
}

.testimonials__controls.desktop {
  display: flex;
  margin: auto;
  justify-content: center;
  padding-top: 5rem;

  @include middle-tablet {
    display: none;
    margin-top: auto;
  }
}

.testimonials__controls.mobile {
  display: none;

  @include middle-tablet {
    display: flex;
    width: 100%;
    justify-content: center;
    margin: 0 auto;
    gap: 0.5rem;
    padding-top: 2rem;
  }
}

.testimonials__controls.mobile .round-button {
  width: 2.35749rem;
  height: 2.35749rem;
}

.testimonials__slider {
  width: 31.25rem;

  @include middle-tablet {
    width: 57rem;
    align-items: center;
    margin-left: 32rem;
    padding-top: 2rem;
  }


}

.testimonials__slide {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ecece8;
  color: #001f1e;
  margin-right: 1.38889rem;
  margin-bottom: 1.66667rem;
  padding: 2.08333rem 2.63889rem;
  width: 400px; // replace with your desired width
  height: 500px; // replace with your desired height
  border-bottom: 1px solid #ecece8;

  @include middle-tablet {
    width: 45%;
    align-items: center;

  }

  @include small-mobile{

  }
}

.testimonials__slide:after {
  content: "";
  border-color: #ecece8 transparent transparent;
  border-style: solid;
  border-width: 4.55556rem 2.24638rem 0 0;
  height: 0;
  width: 0;
  position: absolute;
  bottom: -1.55556rem;

  @include middle-tablet {
    display: none;
  }
}

.testimonials__slide-text {
  font-family: Basis Grotesque Pro;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 1.73611rem;
  width: 100%;

  @include middle-tablet {
    font-size: 14px;
    width: 90%;
  }
}

.testimonials__image-container {
  width: 6.94444rem;
  height: 6.94444rem;
  margin-right: 30px;
  border-radius: 50%;
  -o-object-fit: contain;
  object-fit: contain;

  @include middle-tablet {
  }
}

.testimonials__image-container img {
  height: 100%;
  width: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}

.testimonials__author-container {
  align-items: center;
  display: flex;
}

.testimonials__author-container > div .author,
.testimonials__author-container > div .company {
  display: block;
}

.testimonials__author-container .author {
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 5px;

  @include middle-tablet {
  }
}

.testimonials__author-container .company {
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 1.73611rem;
  width: 90%;

  @include middle-tablet {
  }
}
