// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "CormorantGaramond", sans-serif;
  --second-family: "Merriweather", sans-serif;
  --container-offset: 15px;
  --offset: 32px;

  // sizes

  --main-size: 13px;
  --second-size: 15px;

  // colors
  --light-color: #fff;
  --accent-color: #50c5ff;
  --active-color: rgb(115, 247, 113);
}
