.geolocation {
  background-color: #ebf7f6;
  padding: 0 10% 10rem; // Adjust padding as needed
  display: flex;
  justify-content: center; // Update this to keep the grid in the center
  align-items: center;
  flex-wrap: wrap;

  @include middle-tablet {
    padding: 0 5% 10rem; // Adjust padding as per need
    flex-direction: column;
    align-items: flex-start;
  }

  @include mobile {
    align-items: center;
  }
}

.contact-grid__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr)); // Adjust the minimum size (200px) as needed
  grid-gap: 1.89855rem;
  list-style-type: none;
  width: 100%;
  padding: 0;

  @include middle-tablet {
    grid-template-columns: 1fr;
    width: 60%;
  }

  @include mobile {
    justify-items: center;
    width: 100%;
  }
}

.contact-grid__address {
  width: 100%;
  padding-top: 70%;
  background: #000f0a;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.contact-grid__address a {
  position: relative; /* Add a new stacking context */
  padding-bottom: 1.24638rem;
  display: flex;
  height: 100%;
  width: 100%;
  background: transparent;
  z-index: 1; /* Set a higher z-index */
}

.contact-grid__address:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(rgba(0, 15, 10, 0.6), rgba(0, 15, 10, 0.6));
  z-index: 0; /* Set a lower z-index */
}
.contact-grid__address-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: auto auto 2rem; /* Add a margin-bottom to create space below the content */
  text-align: center;
  color: #fff;
}

.contact-grid__address-content h3 {
  font-family: Freight;
  font-size: 2.16184rem;
  line-height: 10.38647rem;
  margin-bottom: 1.33092rem;
}

.contact-grid__mail {
  position: absolute;
  bottom: 25%; // Adjust these values as needed
  right: 42%; // Adjust these values as needed
  display: flex;
  width: 15%; // Adjust these values as needed
  height: 15%; // Adjust these values as needed
  background-image: url(../../img/svg/email.svg);
  color: white;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  margin: auto;


}
