#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000f0a;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 1s ease-in-out;
}

#preloader.fadeOut {
  opacity: 0;
}

#preloader img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  animation: glow 3s ease-in-out;
}

@keyframes glow {
  0% {
    filter: brightness(1);
    transform: scale(1);
  }
  100% {
    filter: brightness(3);
    transform: scale(1.5);
  }
}

