.accordion {
  display: flex;
  justify-content: flex-end; /* Pushes child elements to the right */
  background-color: #ebf7f6;

}

.accordion__content {
  background-color: #fff;
  padding: 7.69565rem 9.019325rem;
  z-index: 1;
  width: 85%;
  

  @include middle-tablet {
    width: 100%;
    padding: 1rem; // Замените это на желаемое значение

  }
}

.accordion__content-item {
  position: relative;
  cursor: pointer;
}

.accordion__content-item:after {
  bottom: 0;
}

.accordion__content-item:after,
.accordion__content-item:first-child:before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0.5px;
  background-color: #000000;
  transform-origin: left bottom;
  transform: scaleX(1);
  transition: transform .45s cubic-bezier(.65,0,.35,1);
}

.accordion__content-item:first-child:before {
  bottom: 0;
}

.accordion__content-item.isActive .accordion__description {
  max-height: 241.545895rem;
}

.accordion__content-item.isActive .accordion__description-inner {
  opacity: 1;
}

.accordion__content-item.isActive .accordion__more:before {
  opacity: 0;
  transition: opacity .15s cubic-bezier(0,.55,.45,1);
}

.accordion__content-item-wrapper {
  position: relative;
  padding: 0 0;
  min-height: 6.00966rem;
}

.accordion__more {
  width: 1.89855rem;
  height: 1.89855rem;
  top: 0.49275rem;

  .accordion__more.plus:after {
    transform: rotate(90deg);
}

.accordion__more.minus:before {
    opacity: 0;
}
}

.accordion__more,
.accordion__more:before {
  position: absolute;
  right: 0;
  opacity: 1;
  transition: opacity .15s cubic-bezier(.32,0,.67,0) .25s;
  margin: auto;
}

.accordion__more:before {
  content: "";
  left: 0;
  width: 0.5px;
  height: 100%;
  background-color: #001f1e;
}

.accordion__more:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 0.5px;
  background-color: #001f1e;
}

.accordion__title {
  color: #001f1e;
  font-family: Basis Grotesque Pro;
  font-size: 2.381645rem;
  font-weight: 200;
  line-height: 3.985505rem;
  width: 75%;
}

.accordion__description {
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: max-height .425s cubic-bezier(0,.55,.45,1);
}

.accordion__description-inner {
  font-family: Basis Grotesque Pro;
  color: #001f1e;
  font-weight: 200;
  font-size: 1.232365rem;
  line-height: 2.62319rem;
  margin-bottom: 3.62319rem;
  position: relative;
  opacity: 0;
  transition: opacity .2s cubic-bezier(0,.55,.45,1) .1s;
}

.accordion.isInView .accordion__content-item:after,
.accordion.isInView .accordion__content-item:before {
  transform: scaleX(1);
}
