.text-image {
  background-color: #000f0a;

  &__wrapper {
    width: 100%;
    flex-direction: row;
    grid-gap: 0;
    gap: 0;
    justify-content: space-between;
    display: flex;

    @include middle-tablet {
      flex-direction: column;
    }
  }

  &__content {
    margin-left: 5.44444rem;
    padding: 6.38889rem 0 0;
    grid-gap: 2.22222rem;
    gap: 2.22222rem;
    width: 23.43056rem;
    color: white;

    @include middle-tablet {
      width: 90%;
      margin-left: 1rem;
    }

    @include mobile {
      width: 90%;
      margin-left: 1rem;
    }
  }

  &__title {
    font-size: 1.25rem;
    line-height: 1.94444rem;
    letter-spacing: 0.06944rem;
  }

  &__paragraph {
    font-size: 1.11111rem;
    line-height: 1.94444rem;
  }

  &__link {
    display: flex;
    font-size: 1.11111rem;
    line-height: 1.94444rem;
    color: #fff;
    opacity: 0.6 !important;
    position: relative;
    padding-left: 1.73611rem;

    &:before {
      width: 1.04167rem;
      height: 1.04167rem;
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      background-image: url(../../img/svg/link-arrow.svg);
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__picture {
    width: 100%;
    max-width: 37.91667rem;
    height: 40rem; // specify the height
    margin-left: unset;

    @include middle-tablet {
      margin-left: auto;
      max-width: 25.91667rem;
      height: 35rem;
    }

    @include small-tablet {
      max-width: 20.91667rem;
      height: 30rem;
    }

    @include mobile {
      max-width: 10.91667rem;
      height: 10rem;
    }
  }

  &__picture img {
    width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    height: 100%; // make the image fill the height of .first_picture
  }
}
