.discovery {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #000f0a;
  padding-top: 6.875rem;
  grid-gap: 1.73611rem;
  gap: 1.73611rem;
  color: white;
  padding-bottom: 100px;
}

.discovery__title {
  text-transform: uppercase;
  font-size: 1.25rem;
  line-height: 1.94444rem;
  letter-spacing: 0.06944rem;
}

.discovery__paragraph {
  font-size: 1.11111rem;
  line-height: 1.94444rem;
  width: 50%;

  @include middle-tablet {
    width: 90%;
  }
}

.discovery__link {
  display: flex;
  font-size: 1.25rem;
  line-height: 1.25rem;
  padding-left: 1.73611rem;
  color: #fff;
  opacity: 0.6 !important;
  position: relative;
}

.discovery__link:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 1.04167rem;
  height: 1.04167rem;
  background-image: url(../../img/svg/link-arrow.svg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
}
