.identifi--four {
  padding-top: 10vh;
  min-height: 800px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  color: var(--light-color);
  background-image: url("../../img/svg/feats.png");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  .identifi__content {
    margin: 2rem auto 0.5rem;
    /* Adjusted top margin to 2rem and bottom margin to 0.5rem */
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    text-align: center;
  }

  .identifi__title {
    margin: 0 auto;
    margin-bottom: 3rem;
    /* Decreased bottom margin to 2rem */
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 4rem;
    /* Decreased font size to 4rem */
    line-height: 3.6rem;
    /* Decreased line height to 3.6rem */
    letter-spacing: 1.2px;
    text-align: center;

    @include mobile {
      font-size: 2rem;
      margin: 0 auto;
      margin-bottom: 2rem;

    }
  }

  .identifi__descr {
    margin: 0;
    margin-bottom: 2rem;
    /* Decreased bottom margin to 2rem */
    font-weight: 400;
    font-size: 1.4rem;
    /* Decreased font size to 1.4rem */
    font-family: var(--second-family);
    line-height: 2.4rem;
    /* Decreased line height to 2.4rem */
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }


}

