.text-image.isRevert {
  color: #001f1e;
}

.text-image.isRevert .text-image__wrapper {
  background: #ebf7f6;
  margin-left: unset;
  flex-direction: row-reverse;
  padding: 0rem 0;
  width: 95%;

  @include middle-tablet {
    flex-direction: column;
    width: 75%;
  }

  @include small-tablet {
    flex-direction: column;
    width: 85%;
  }


}

.text-image.isRevert .text-image__content {
  padding: 8.125rem 0 0;
  margin-right: 5.69444rem;
  margin-left: unset;
  width: 29.65278rem;
  color: black;


  @include middle-tablet {
    margin-left: 3rem;
  }

  @include small-tablet {
    width: 90%;
    margin-left: 1rem;

  }


  @include mobile {
    width: 90%;
    margin-left: 1rem;

  }


}

.text-image.isRevert .text-image__link {
  color: #001f1e
}

.text-image.isRevert .text-image__link:before {
  background-image: url(../../img/svg/black.svg)
}

.text-image.isRevert .text-image__picture {
  margin-left: 1rem;
  height: 90%;

  @include middle-tablet {
    margin-left: 3rem;
  }
}


